.bg-wave {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  &::before {
    content: "";
    width: 100%;
    height: 10.5vw;
    background: url(/assets/images/bg_wave_top.png) no-repeat center top;
    background-size: contain;
    position: absolute;
    top: -3px;
    left: 0;
    z-index: 2;
  }
  &::after {
    content: "";
    width: 100%;
    height: 10.5vw;
    background: url(/assets/images/bg_wave_bottom.png) no-repeat center bottom;
    background-size: contain;
    position: absolute;
    bottom: -10px;
    left: 0;
    z-index: 2;
  }
}
.illust {
  position: absolute;
  z-index: 2;
  pointer-events: none;
  &.illust01 {
    max-width: 593px;
    top: 130px;
    right: 0;
    @include max(1500px) {
      width: 39.333vw;
    }
  }
  &.illust02 {
    max-width: 900px;
    top: 110px;
    left: 0;
    @include max(1500px) {
      width: 50vw;
    }
  }
  &.illust03 {
    max-width: 750px;
    top: 0;
    left: 0;
    @include max(1500px) {
      width: 45vw;
    }
  }
  &.illust04 {
    max-width: 650px;
    right: 0;
    bottom: 0;
    @include max(1500px) {
      width: 40vw;
    }
  }
  &.illust05 {
    max-width:364px;
    top: 0;
    left: 0;
    mix-blend-mode: exclusion;
    @include max(1500px) {
      width:25vw;
    }
  }
  &.illust06 {
    max-width: 405px;
    right: 0;
    bottom: -7vw;
    mix-blend-mode: exclusion;
    @include max(1500px) {
      width: 27vw;
    }
  }
}

.under_txt {
  font-size: 22px;
  font-weight: 600;
  text-align: center;
  margin-bottom: 35px;
  @include max(1024px) {
    font-size: 18px;
  }
}
